import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import { useFormik } from 'formik';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

import { Loader, LocationField, TextField } from '../components';

import { LocationQuery } from '../api';
import { useSnackbar } from '../hooks';
import { CreateLocation } from '../utils/dataTypes';

const initialValues = {
  id: '',
  zipCode: '',
  name: '',
  stateCode: '',
  stateName: '',
  location: [NaN, NaN] as [number, number],
};

export default function ViewLocationForm() {
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    data,
  } = useQuery({
    queryKey: ['location', pathname.split('/')[3]],
    queryFn: LocationQuery,
    keepPreviousData: false,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<CreateLocation>({
    initialValues: {
      ...initialValues,
      ...data,
    },
    enableReinitialize: true,
    onSubmit: () => {
      return;
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  const handleEdit = () => {
    navigate(pathname.replace('view', 'edit'), { replace: true });
  };

  return (
    <Box>
      <form onSubmit={formik.handleSubmit} onReset={formik.handleReset}>
        <Box sx={{ p: 3, display: 'grid' }}>
          <Box key="controls" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, width: '50%' }}>
              <TextField
                fieldName="name"
                label="Name"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="stateName"
                label="State Name"
                disabled
                formik={formik}
              />
            </Box>
            <Box key="right" sx={{ p: 1, width: '50%' }}>
              <TextField
                fieldName="zipCode"
                label="Zip Code"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="stateCode"
                label="State Code"
                disabled
                formik={formik}
              />
            </Box>
          </Box>
          <Box key="location" sx={{ marginTop: -2, display: 'inline-flex' }}>
            <LocationField
              fieldName="location"
              label={['Latitude', 'Longitude']}
              disabled
              formik={formik}
            />
          </Box>
          <Box key="buttons" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleEdit}
              >
                Edit
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={loading || fetching} />
    </Box>
  );
}
